.heading {
    font-size: 2rem;
    width: 100%;
    text-align: center;
    margin-bottom: 2rem;
    opacity: 0;
  }
  
  .text {
    font-size: 1.2rem;
    text-align: center;
    opacity: 0;
  }
  
  @media only screen and (max-width: 1045px) {
    .heading {
      font-size: 1.5rem;
      text-align: left;
    }
  
    .text {
      font-size: 1rem;
      text-align: left;
    }
  }