@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;700&display=swap);
body {
  font-family: "Lato", sans-serif;
  background-color: #f5f6fd;
  color: #212841;
  margin: 0;
  padding: 0;
}

html {
  scroll-behavior: smooth;
}

a {
  color: inherit;
  text-decoration: none;
}

p {
  margin: 0;
}

.navbar {
  position: fixed;
  width: 100%;
  box-sizing: border-box;
  z-index: 10;
  background-color: #f5f6fd;
  opacity: 0;
}

.nav-links {
  list-style: none;
  margin: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
  padding: 2rem 8rem;
}

.nav-link {
  margin-left: 3rem;
  cursor: pointer;
}

.contact {
  background-color: #43b97f;
  padding: 0.5rem 1rem;
  color: #f5f6fd;
  border-radius: 8px;
}

.nav-link:hover {
  text-decoration: underline;
}

.contact:hover {
  background-color: #33885f;
  text-decoration: none;
}

.landing {
  display: flex;
  justify-content: space-between;
  align-items: center;
  min-height: 100vh;
  box-sizing: border-box;
  padding: 8rem;
  position: relative;
}

h2,
h3 {
  margin: 0;
}

.main-heading {
  font-size: 3.5rem;
  opacity: 0;
}

.sub-heading {
  font-size: 2rem;
  margin-top: 1rem;
  opacity: 0;
}

.landing-il {
  opacity: 0;
}

.orange {
  color: #ff511a;
}

.green {
  color: #43b97f;
}

img {
  height: 250px;
}

.chevron {
  display: none;
}

/* Chevron animation from https://codepen.io/rightblog/pen/EagNMN/*/
*,
*:before,
*:after {
  box-sizing: border-box;
}

.mouse_scroll {
  position: absolute;
  margin: 0 auto;
  width: 24px;
  height: 100px;
  bottom: 0;
  display: none;
}

.m_scroll_arrows {
  display: block;
  width: 5px;
  height: 5px; /* IE 9 */
  -webkit-transform: rotate(45deg); /* Chrome, Safari, Opera */
  transform: rotate(45deg);

  border-right: 2px solid #43b97f;
  border-bottom: 2px solid #43b97f;
  margin: 0 0 3px 4px;

  width: 16px;
  height: 16px;
}

.unu {
  margin-top: 1px;
}

.unu,
.doi,
.trei {
  -webkit-animation: mouse-scroll 1s infinite;
  animation: mouse-scroll 1s infinite;
}

.unu {
  -webkit-animation-delay: 0.1s;
  -moz-animation-delay: 0.1s;
  -webkit-animation-direction: alternate;

  animation-direction: alternate;
  -webkit-animation-delay: alternate;
          animation-delay: alternate;
}

.doi {
  -webkit-animation-delay: 0.2s;
  -moz-animation-delay: 0.2s;
  -webkit-animation-direction: alternate;

  animation-delay: 0.2s;
  animation-direction: alternate;

  margin-top: -6px;
}

.trei {
  -webkit-animation-delay: 0.3s;
  -moz-animation-delay: 0.3s;
  -webkit-animation-direction: alternate;

  animation-delay: 0.3s;
  animation-direction: alternate;

  margin-top: -6px;
}

.mouse {
  height: 42px;
  width: 24px;
  border-radius: 14px;
  -webkit-transform: none;
          transform: none;
  border: 2px solid #43b97f;
  top: 170px;
}

.wheel {
  height: 5px;
  width: 2px;
  display: block;
  margin: 5px auto;
  background: #43b97f;
  position: relative;

  height: 4px;
  width: 4px;
  border: 2px solid #43b97f;
  border-radius: 8px;
}

.wheel {
  -webkit-animation: mouse-wheel 0.6s linear infinite;
  animation: mouse-wheel 0.6s linear infinite;
}

@-webkit-keyframes mouse-wheel {
  0% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }

  100% {
    opacity: 0;
    -webkit-transform: translateY(6px);
    transform: translateY(6px);
  }
}
@keyframes mouse-wheel {
  0% {
    top: 1px;
  }
  25% {
    top: 2px;
  }
  50% {
    top: 3px;
  }
  75% {
    top: 2px;
  }
  100% {
    top: 1px;
  }
}

@-webkit-keyframes mouse-scroll {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
@keyframes mouse-scroll {
  0% {
    opacity: 0;
  }
  50% {
    opacity: 0.5;
  }
  100% {
    opacity: 1;
  }
}
/* Chevron animation ends here */

@media only screen and (max-width: 1315px) {
  img {
    height: 200px;
  }

  .main-heading {
    font-size: 2rem;
  }

  .sub-heading {
    font-size: 1.5rem;
    margin-top: 1rem;
  }
}

@media only screen and (max-width: 1045px) {
  .landing {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 0;
  }

  article {
    text-align: center;
    margin-bottom: 3rem;
  }

  .navbar {
    display: none;
  }

  img {
    height: 130px;
  }

  .main-heading {
    font-size: 1.5rem;
  }

  .sub-heading {
    font-size: 1rem;
    margin-top: 0.5rem;
  }

  .mouse_scroll,
  .chevron {
    display: block;
  }
}

.projects {
  min-height: 50vh;
  padding: 6rem 8rem;
  box-sizing: border-box;
}

.proj-cont {
  margin-top: 2rem;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  overflow-x: scroll;
  padding: 2rem;
  opacity: 0;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.proj-cont::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.proj-cont {
  -ms-overflow-style: none; /* IE and Edge */
  scrollbar-width: none; /* Firefox */
}

.proj {
  padding: 1rem;
  box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.02),
    0 6.7px 5.3px rgba(0, 0, 0, 0.028), 0 12.5px 10px rgba(0, 0, 0, 0.035),
    0 22.3px 17.9px rgba(0, 0, 0, 0.042), 0 41.8px 33.4px rgba(0, 0, 0, 0.05),
    0 100px 80px rgba(0, 0, 0, 0.07);
  width: 30rem;
  display: flex;
  flex-direction: column;
  border-radius: 8px;
  margin-right: 2rem;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2);
}

.proj:hover {
  box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
}

.proj-img {
  height: 15rem;
  width: 28rem;
  margin-bottom: 1rem;
  object-fit: cover;
  border-radius: 8px;
}

.orange {
  color: #ff511a;
}

.proj-heading {
  margin-bottom: 0.5rem;
  color: #2b2c47;
}

@media only screen and (max-width: 1045px) {
  .projects {
    padding: 0;
    padding-left: 2rem;
    min-height: inherit;
  }

  .proj-img {
    height: 8rem;
    width: 12rem;
  }

  .proj-cont {
    padding-left: 0;
    margin-top: 0;
  }

  .proj {
    margin-right: 1rem;
  }
}

.skills {
  padding: 6rem 8rem;
  min-height: 50vh;
  box-sizing: border-box;
}

.skill-cont {
  width: 100%;
  height: 15rem;
  display: flex;
  justify-content: flex-start;
  align-items: stretch;
  overflow: scroll;
  opacity: 0;
}
.skill-img {
  height: 8rem;
  width: 8rem;
  margin-bottom: 1rem;
  object-fit: cover;
  border-radius: 8px;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.skill-cont::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.skill-cont {
  -ms-overflow-style: none;
  scrollbar-width: none;
}

.skill {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  margin-right: 4rem;
}

.progress-bar {
  background-color: lightgray;
  height: 8rem;
  content: none;
  width: 2rem;
  margin-bottom: 1rem;
  border-radius: 8px;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}

.bar {
  background-color: #43b97f;
  content: none;
  height: 50%;
  border-radius: 8px;
  margin-top: auto;
}
.skill0 {
  width: 50%;
  height: 50%;
}
@media only screen and (max-width: 1045px) {
  .skills {
    padding: 0;
    padding-left: 2rem;
    min-height: inherit;
  }

  .skill-cont {
    margin-right: 1rem;
    margin-bottom: 0;
  }
}

.about {
  min-height: 50vh;
  padding: 6rem 8rem;
  box-sizing: border-box;
}
.about-div {
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.about-pic {
  border-radius: 50%;
  height: 25rem;
  flex: 1 1;
  margin-left: 12rem;
}
.about-picc {
  border-radius: 50%;
  height: 350px;
  width: 300px;
  margin-left: -9rem;
  margin-top: -4rem;
}

.about-text {
  margin-bottom: 5rem;
  flex: 2 1;
  height: 20rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  justify-self: center;
  line-height: 50px;
}
.about-font {
  font-family: Georgia, "Times New Roman", Times, serif;
}
.text {
  height: 15rem;
  margin-bottom: -35rem;
}
.btn {
  padding: 1rem 1.5rem;
  text-align: center;
  margin-top: 40rem;
  font-weight: 600;
  font-size: 1.2rem;
  color: rgb(62, 168, 175);
}

.btn-primary {
  border: 2px solid #333;
  border-radius: 10px;
  transition: all 0.3s ease-out;
}

.btn-primary:hover {
  background: #0ce27a;
  border: 2px solid #43b97f;
  color: #fff;
}

.heading {
  margin-bottom: 2rem;
  text-align: center;
}

.about .heading {
  margin-bottom: 7rem;
  text-align: center;
  margin-left: 2rem;
  font-size: 50px;
}
.copyright {
  float: left;
  margin-left: -6rem;
  margin-bottom: -9rem;
}
@media only screen and (max-width: 1045px) {
  .about {
    padding: 2rem;
    min-height: inherit;
  }
}

